#background {
  background-color: #001220;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
}
#waves {
  bottom: 0;
  left: 0;
  width: 100%;
  position: absolute;
}
#text-container {
  top:15%;
  left: 0;
  position: absolute;
  width: 100%;
  height: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.size-1 {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  text-align: center;
  font-size: 49px;
  font-style: normal;
  font-weight: 600;
}
.size-2 {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  text-align: center;
}
.color-1 {
  color: #00FFF0;
}
.color-2 {
  color: #089EFF;
}
#navbar {
  top:0;
  left: 0;
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  z-index: 1;
}
#title {
  width: 430px;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  z-index: 1;
}
#blank {
  width: 25%;
  height: 100%; 
}
#links {
  width: 45%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  z-index: 1;
}
.link {
  color: #FFF;
  text-align: center;
  font-family: Montserrat;
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  z-index: 1;
}
#button-container {
  width: 30%;
  margin-top: 3%;
  height: 10%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}
.button {
  color: #FFF;
  text-align: center;
  font-family: Montserrat;
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
#register {
  width: 220px;
  height: 50px;
  border-radius: 128px;
  background: #089EFF;
  display: flex;
  justify-content: center;
  align-items: center;
}
a{
  cursor: pointer;
  text-decoration: none;
}